export const onSectionCreated = EventDefinition(
  'wix.restaurants.menus.v1.section_created',
  true,
  (event: SectionCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'entity.createdDate' },
            { path: 'entity.updatedDate' },
            {
              path: 'entity.orderSettings.availability.timestampRangesOptions.ranges.startTime',
            },
            {
              path: 'entity.orderSettings.availability.timestampRangesOptions.ranges.endTime',
            },
            { path: 'entity.externalReferenceInfo.lastSyncDate' },
            { path: 'metadata.eventTime' },
          ],
        },
        {
          transformFn: transformRESTImageToSDKImage,
          paths: [
            { path: 'entity.image' },
            { path: 'entity.additionalImages', isRepeated: true },
          ],
        },
      ])
    )
)<SectionCreatedEnvelope>();
